body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  background: linear-gradient(to left, rgb(3, 23, 78, 1) 0%, rgb(0, 124, 124, 1) 100%);
}

body {
  background: linear-gradient(to left, rgba(7, 27, 82, 1) 0%, rgba(0, 128, 128, 1) 100%);
}

.footer {
  background: linear-gradient(to left, rgba(17, 27, 92, 3) 0%, rgba(0, 138, 138, 3) 100%);
}

img {
  width: 100px;
}

.contact {
  width: 50px;
}

a {
  color: white;
  text-decoration: none;
}

h1,
h2,
h4,
h5,
p {
  color: white;
}

